.catalog__gift {
    background-color: $base-color;
    max-width: 80%;
    min-height: 290px;
    box-sizing: border-box;

    margin-right: auto;
    margin-left: auto;
    margin-bottom: 5%;
    padding: 10%;
    box-sizing: border-box;

    @include flex($direction: column);
    align-items: center;
}

.catalog__gift-text {
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
    margin-top: auto;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    max-width: 80%;
    padding: 0.2em;
}

@media screen and (min-width: $tablet-width) {
    .catalog__gift {
        width: 100%;
        max-width: 100%;
        padding: 60px 120px;
        @include flex($direction: row);
        margin-bottom: 70px;
    }

    .catalog__gift-text {
        max-width: 159px;
        margin-right: 0;
        margin-top: 0;
    }
}

@media screen and (min-width: $desktop-width) {
  .catalog__gift {
    width: 245px;
    max-width: 245px;
    margin-right: 0;
    padding: 43px;
    background-image: url("../../images/gift-photo-desktop.jpg");
    background-size: cover;
    background-repeat: no-repeat;
  }

  .catalog__gift-text {
    position: relative;
    width: 100%;
    max-width: 100%;
    margin-right: 0;
    margin-top: auto;

    &::before {
      content: url("../../images/icons/gift.png");
      position: absolute;
      top: -100px;
      left: 35%;
      width: 55px;
      height: 60px;
    }
  }
}
