@mixin flex($direction: row) {
  display: flex;
  flex-direction: direction;
  flex-wrap: wrap;
}

@mixin list() {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin indent($margin:0, $padding: 0) {
  margin: $margin;
  padding: $padding;
}

@mixin margin-auto() {
    margin-right: auto;
    margin-left: auto;
}

@mixin placeholder() {
    color: $light-color;
    font-family: "Oswald", Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 18px;
    text-transform: uppercase; 
}