.product-types {
    width: $mobile-width;
    padding: 9.375%;
    box-sizing: border-box;
    @include flex($direction: column);
    justify-content: flex-start;
    align-items: center;
}

.product-types__target {
    width: 100%;
    box-sizing: border-box;
    padding: 9.375%;
    margin-bottom: 10%;
    @include margin-auto();
    background-color: $bg-color;
}

.product-types__heading {
    color: $dark-color;
    font-family: "Oswald", Arial, Helvetica, sans-serif;
    font-size: 24px;
    font-weight: 400;
    line-height: 1.5;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 10%;
}

.product-types__paragraph {
    color: $light-color;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.5;
    text-align: left;
}

.product-types__target-link {
    position: relative;
    color: $dark-color;
    font-family: "Oswald", Arial, Helvetica, sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    text-transform: uppercase;
    text-decoration: none;

    &::after {
        content: "";
        position: absolute;
        top: 25%;
        left: 105%;
        width: 24px;
        height: 11px;
        background-color: $dark-color;
    }
}

@media screen and (min-width: $tablet-width) {
    .product-types {
        width: 100%;
        padding: 0;
        padding-right: 5%;
        padding-left: 5%;
    }

    .product-types__target {
        padding-left: 5%;
        padding-right: 50%;
        padding-top: 10%;
        padding-bottom: 10%;
        margin-bottom: 5%;
        text-align: left;
    }

    .product-types__heading {
        font-size: 36px;
        margin-bottom: 10%;
        text-align: left;
        padding-left: 0;
    }

    .product-types__paragraph {
        margin-bottom: 10%;
    }
}

@media screen and (min-width: $desktop-width) {
  .product-types {
    flex-direction: row;
    justify-content: space-between;
  }

  .product-types__target {
    max-width: 45%;
    padding: 30px;
    box-sizing: border-box;
  }

  .product-types__heading {
    position: relative;
    text-align: center;
    padding-left: 100px;

    &::before {
      content: "";
      position: absolute;
      top: -50%;
      left: 0;
      width: 100px;
      height: 100px;
      background-repeat: no-repeat;
    }
  }

  .product-types__target--slim .product-types__heading::before {
    background-image: url("../../images/cat1-desctop.png");
  }

  .product-types__target--pro .product-types__heading::before {
    background-image: url("../../images/cat2-desctop.png");
    width: 134px;
  }

}
